.slick-slide{
    margin: 0 20px;
}

.slick-slide img{
    width: 100%;
  height: auto;
}

.slick-list{
    display: block;
    position: relative;
    overflow: hidden;
    margin :0;
    padding: 0;


}

.slick-list:focus{
    outline:none,


}

.slick-slide{
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;

}

.slick-slide img{
    display: block;
   
  }
  
  .slick-initialized .slick-slide{
      display: block;
  }

  .slick-loading .slick-slide{
      visibility: hidden;

  }


  

  .slick-dots {
	display: flex;
	justify-content: center;
	
	margin: 0;
	padding: 1rem 0;
	
	list-style-type: none;
  }

  .slick-dots li {
    margin: 0 0.25rem;

  
}

.slick-dots li button {
    display: block;
    width: 1rem;
    height: 1rem;
    padding: 0;
  
    border: none;
    border-radius: 100%;
    background-color:  var(--hovercolor)  !important;
    
    text-indent: -9999px;
}
.slick-dots  li.slick-active button {
    background-color: var( --headingcolor) !important;
    width: 50px !important;
    border-radius: 20px;
    
    
  
}

