.smmmarketing{
  
    margin-top: 70px;
    background-image: url("../images/socialmediabackimg.jpg");
    padding:10px;
   
}

.smmmarketingrow{
  
}

.smmmarketingleft{
   padding:10px;
}

.smmmarketingleft h3{
   
     
    font-family: 'Nunito', sans-serif;
    font-size: 36px;
    font-weight: 800;
    line-height: 43.2px;
    color: var( --headingcolor);
    text-align: left;
  
  }

  .smmmarketingleft p{

    color: var( --headingcolor);
  text-align: left;
  font-family: 'Rubik', sans-serif;
  font-size: 16px;
  margin-top: 50px;


  }
  .SMMbtnsection{
     
      margin-top: 40px;;
      margin-bottom: 40px;
  }

  .SMMbtnsection button{
  
    height: 50px;
    font-family: 'Rubik', sans-serif;
      font-weight: 500;
      border-radius: 10px;
      border: none;
      line-height: 16.8px;
      text-transform: uppercase;
      width: 175px;


   
}

.SMMbtnsection .SMMbtn1{
    background: linear-gradient(to left,#f0a056 ,#ec5c6b);
  
   
    color: white; 
  
  }
  .SMMbtnsection .SMMbtn1:hover{
    background: #ec5c6b;
    color:white;
    transition: 0.3;;
  }
  .SMMbtnsection .SMMbtn2{
    background: var(--headingcolor);
    color: white;
    margin-left: 20px;
   
  }
  .SMMbtnsection .SMMbtn2:hover{
    background: white;
    border:2px solid var(--headingcolor); 
    color: var(--headingcolor);
    border: 2px solid var(--headingcolor);
   
  }
  
  /* socialmediamarketing */









.SMMcol{
  
    margin-bottom: 25px;
  
  
  }
  .SMMcol .card{
   
    padding:20px !important;
    border-radius: 24px;
    width: 263px;
       box-shadow: 0px 0px 60px #EFF2F5;
    height: 224.23px;
  }
  
  .SMMcol .card h4{
    margin-left: 10px ;
    font-family: 'Rubik', sans-serif;
    color: var(--headingcolor);
    font-size: 18px;
    line-height: 21.6px;
    font-weight: 600;

    text-align: left;
    
   margin-top: 5px;
  
  }
  
  .SMMcol .card p{
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
    color: var(--headingcolor);
    font-size: 14px;
    line-height: 16.8px;
    text-align: left;
    padding-top:15px;
    padding-bottom: 15px;
  
  }
  .SMMcol .SMM .SMMimg{
    background-image: url("../images/groupcontactorangepng.png") !important;
   
      background-repeat: no-repeat;
    background-size: contain;
  }
  .SMMcol .SMM:hover .SMMimg{
    background-image: url("../images/groupcontactwhitepng.png") !important;
   
      background-repeat: no-repeat;
    background-size: contain;
  }
  .SMMcol .SMM:hover h4 {
    color: white;
  }
  .SMMcol .SMM:hover  p{
    color: white;
  }
  .SMMcol .SMM:hover {
  background-color: #E25C43;
  
  }
  .SMScol .SMS:hover h4 {
    color: white;
  }
  .SMMcol .SMS:hover  p{
    color: white;
  }
  .SMMcol .SMS:hover {
  background-color:  #28cfe8
  
  }
